@import '404';
@import 'search-results';
#page {
    overflow: hidden;
    position: relative;
    width: 100%;
}

img {
    max-width:100%;
    height:auto;
   
}
// sections
.section {
    padding: 50px 0;

    @include breakpoint(md) {
        padding: 60px 0;
    }

    @include breakpoint(xl) {
        padding: 80px 0;
    }

    &.p-small {
        padding: 40px 0;

        @include breakpoint(md) {
            padding: 50px 0;
        }

        @include breakpoint(xl) {
            padding: 80px 0;
        }
    }
    &.p-large {

        padding: 60px 0;

        @include breakpoint(md) {
            padding: 80px 0;
        }

        @include breakpoint(xl) {
            padding: 120px 0;
        }
    }
}

.container {
    @include breakpoint(md) {
        padding-right: ($grid-gutter-width / 2);
        padding-left: ($grid-gutter-width / 2);
    }
}

.secondary-bg {
    background:$secondary-bg;
}

.screen-reader-text {
    display:none;
}

// link
.link-icon {
    img, svg {
        display: inline-block;
        vertical-align:middle;
        width:20px;
        height:20px;
        margin-right:10px;
    }
}