.site-header {
    background: $white;
    @include transition;
    .navbar {
        height:$headerHeight;
        padding: 0;
    }

    // simple fad in down animation on scroll
    .move-header-top & {
        // animation-duration: .5s;
        // animation-fill-mode: both;
        // animation-name: fadeInDown
    }
    // menu toggle icon
    #site-header-menu-toggle {
        position: relative;
        z-index: 11;
        border-radius: 0;
        background: none;
        padding: 0;
        border: none;
        line-height: 1;
        width: 25px;
        height: 16px;
        -webkit-transform: rotate(0deg);
        -moz-transform: rotate(0deg);
        -o-transform: rotate(0deg);
        transform: rotate(0deg);
        @include transition(.5s ease-in-out);
        cursor: pointer;

        &:hover,
        &:focus {
            border: none;
            outline: none;
            box-shadow: none;
        }

        span {
            display: block;
            position: absolute;
            height: 2px;
            width: 100%;
            background: $black;
            border-radius: 10px;
            opacity: 1;
            left: 0;
            -webkit-transform: rotate(0deg);
            -moz-transform: rotate(0deg);
            -o-transform: rotate(0deg);
            transform: rotate(0deg);
            -webkit-transition: .25s ease-in-out;
            -moz-transition: .25s ease-in-out;
            -o-transition: .25s ease-in-out;
            transition: .25s ease-in-out;

            &:nth-child(1) {
                top: 0px;
            }

            &:nth-child(2) {
                top: 7px;
            }

            &:nth-child(3) {
                top: 14px;
            }

        }

        &.toggle-open {
            span {
                background: $white;
            }
            span:nth-child(1) {
                top: 9px;
                -webkit-transform: rotate(135deg);
                -moz-transform: rotate(135deg);
                -o-transform: rotate(135deg);
                transform: rotate(135deg);
            }

            span:nth-child(2) {
                opacity: 0;
                left: -20px;
            }

            span:nth-child(3) {
                top: 9px;
                -webkit-transform: rotate(-135deg);
                -moz-transform: rotate(-135deg);
                -o-transform: rotate(-135deg);
                transform: rotate(-135deg);
            }

        }
    }


    // logo
    &-logo {
        line-height:0;
    }

    // menu
    &-naviation {
        // only for mobile
        @include breakpointMax(xl){
            position:fixed;
            top:-50px;
            left:0;
            width:100%;
            // max-width:300px;
            height:100vh;
            overflow:hidden;
            background:lighten($primary, 15);
            padding-top:$headerHeight;
            z-index:10;
            visibility:hidden;
            opacity:0;
            @include transition;
        }
        &.site-header-naviation-open {
            left:0;
            top:0;
            visibility:visible;
            opacity:1;
        }
        .primary-menu {
            @include breakpointMax(xl){
                max-height: 100%;
                overflow-y: auto;
            }
            .menu {
                list-style:none;
                @extend .m-0;
                @extend .d-flex;
                @extend .flex-wrap;
                @extend .align-items-center;
                &-item {
                    @extend .m-0;
                    padding:10px 0;
                    width:100%;
                    @include breakpoint(xl) {
                        width: auto;
                        padding:18px 0;
                    }
                    a {
                        color:white;
                        padding:0 45px 0 16px;
                        font-size:24px;
                        display: block;
                        @include breakpoint(xl) {
                            color:$black;
                            font-size:16px;
                            padding:0 12px;
                        }
                        &:hover {
                            color: $black;
                            @include breakpoint(xl) {
                                color:$primary;
                            }
                        }
                    }
                    // is current page
                    &.current_page_item {
                        a {
                          color: $primary;
                        }
                    }
                    // has children Level 1
                    &.menu-item-has-children {
                        position:relative;
                        & > a {
                            @include breakpointMax(xl) {
                                &:after {
                                    display:none;
                                }
                            }
                        }
                        .toggle-sub-menu {
                            border:none;
                            outline:none;
                            padding:0px;
                            background:none;
                            width: 30px;
                            height: 30px;
                            right: 10px;
                            top:13px;
                            position:absolute;
                            z-index:3;
                            @include breakpoint(xl) {
                                display:none;
                            }
                            &:after, &:before {
                                content: '';
                                display: block;
                                background:white;
                                @include xy_center;

                            }
                            &:before{
                                height: 16px;
                                width:2px;
                                @include transition;
                            }
                            &:after{
                                width: 16px;
                                height:2px;
                            }
                            &.toggle-open {
                                &:before {
                                    height:0;
                                }
                            }
                        }
                        & > .sub-menu {
                            display:none;
                            list-style:none;
                            @extend .m-0;
                            padding: 5px 0;
                            @include breakpoint(xl) {
                                padding: 40px 0;
                                position:absolute;
                                top:100%;
                                left:0;
                                background:$white;
                                border-radius:6px;
                                border-top: 1px solid $gray-100;
                                box-shadow: 0 4px 4px rgba(0,0,0,.25);
                            }
                            .menu-item {
                                white-space: nowrap;
                                padding:5px 0px 5px 15px;
                                @include breakpoint(xl) {
                                  padding:5px 0;
                                }
                                a {
                                   font-size:18px;
                                   font-weight:$font-weight-normal;

                                   @include breakpoint(xl) {
                                       padding:0 40px;
                                        font-size:14px;
                                   }
                                }
                                // has children Level 2 or more
                                &.menu-item-has-children {
                                    & > a {
                                        &:after {
                                            @include transform(rotate(-90deg));
                                        }
                                    }
                                    .toggle-sub-menu {
                                        top:3px;
                                    }
                                    & > .sub-menu {

                                        display:none;
                                        @include breakpoint(xl) {
                                            top:0;
                                            left:100%;
                                        }
                                    }
                                    &:hover {
                                        & > .sub-menu {
                                            @include breakpoint(xl) {
                                                display:block;
                                            }
                                        }
                                    }
                                }
                            }
                        }
                        // mega menu
                        &.has-megamenu {
                            & > a {
                                @include breakpoint(xl){
                                    position: relative;

                                }
                            }
                            @include breakpoint(xl){
                             position: static;
                            }
                            & > .sub-menu {
                                @include breakpoint(xl){
                                    padding-left:20px;
                                    padding-right:20px;
                                    padding-bottom:0px;
                                }
                                & > li.menu-item {
                                    @include breakpoint(xl){
                                        float:left;
                                        text-align:left;
                                        white-space:normal;
                                        border-right: 1px solid $gray-100;
                                        margin-bottom:40px !important;
                                        a {
                                            padding:0 22px;
                                        }
                                    }


                                }
                                .menu-item-has-children {

                                    & >a {
                                        @include breakpoint(xl){
                                            position: relative;
                                            font-size:16px;
                                            font-weight:$font-weight-medium;
                                        }
                                        &:after {
                                            display:none;
                                        }
                                    }
                                    .sub-menu {
                                        @include breakpoint(xl){
                                            background:none;
                                            box-shadow:none;
                                            border-radius:0;
                                            border:none;
                                            position:static;
                                            display:block;
                                            padding:15px 0 0;
                                            li.menu-item {
                                                width: 100%;
                                            }
                                        }
                                        &:after {
                                            content: '';
                                            clear:both;
                                            display:block;
                                        }
                                    }
                                }
                            }


                            &.sub-menu-col-two {
                                .sub-menu {
                                    @include breakpoint(xl){
                                        width:425px;
                                        left: auto;
                                        & > li.menu-item {
                                            width:50%;
                                             &:nth-child(2n + 0) {
                                                border-right:none;
                                            }
                                        }
                                    }
                                }
                            }
                            &.sub-menu-col-three, &.sub-menu-col-four{
                                @include breakpoint(xl){

                                 position: static;
                                     .sub-menu {
                                        left:0;
                                        right:0;
                                        width:100%;
                                        box-shadow:none;
                                        border-radius:0;
                                    }
                                }
                            }
                            &.sub-menu-col-three {
                                .sub-menu {
                                    & > li.menu-item {
                                        @include breakpoint(xl){
                                            width:33.3333%;
                                            &:nth-child(3n + 0) {
                                                border-right:none;
                                            }
                                        }
                                    }
                                }
                            }
                            &.sub-menu-col-four{
                                .sub-menu {
                                    li.menu-item {
                                        @include breakpoint(xl){
                                            width:25%;
                                            &:nth-child(4n + 0) {
                                                border-right:none;
                                            }
                                        }
                                    }
                                }
                            }
                        }
                        &:hover {
                            & > .sub-menu {
                                @include breakpoint(xl) {
                                    display:block;
                                }
                            }
                        }

                    }
                }
            }
        }
    }

    &-right {
        .btn {
            min-width:auto;
        }
    }
    // site header menu right align
    &.site-header-right-menu {
        .site-header-naviation {
            @include breakpoint(xl) {
                margin-right:80px;
            }
        }
    }
}

// Manage top padding if header is fixed
.page-with-fixed-header {
    .site-header {
        position:fixed;
        top:0;
        left:0;
        right:0;
        width:100%;
        z-index:9;
    }
    .site-main {
        padding-top: $headerHeight;
    }
}

// If user logged in
.logged-in.admin-bar  {
    .site-header {
        top: 46px;
        @include breakpoint(lg) {
            top: 32px;
        }
    }
    .site-header-naviation {
        top: 46px;
        @include breakpoint(lg) {
            top: 32px;
        }
    }
    // move header to top on mobile scroll
    .move-header-top {
        .site-header {
            top: 0px;
        }
        .site-header-naviation {
            top: 0px;
        }
    }

}





// mobile menu overlay
#bodyClick {
    height: 100%;
    width: 100%;
    position: fixed;
    display: none;
    top: -100%;
    left: 0;
    content: "";
    z-index: 9;
    overflow-x: hidden;
    background-color: rgba($black, 0.6);
    @include transition (0.50s,
    cubic-bezier(0.685,
    0.0473,
    0.346,
    1));
    &.menu-open {
        display:block;
        animation-duration: .35s;
        animation-fill-mode: both;
        animation-name: fadeInDown;
    }


}


// header animation
@-webkit-keyframes fadeInDown {
    0% {
        opacity: 0;
        top: -100%
    }

    100% {
        opacity: 1;
        top: 0
    }
}

@-moz-keyframes fadeInDown {
    0% {
        opacity: 0;
        top: -100%
    }

    100% {
        opacity: 1;
        top: 0
    }
}

@-o-keyframes fadeInDown {
    0% {
        opacity: 0;
        top: -100%
    }

    100% {
        opacity: 1;
        top: 0
    }
}

@keyframes fadeInDown {
    0% {
        opacity: 0;
        top: -100%
    }

    100% {
        opacity: 1;
        top: 0
    }
}


