.nav-links {
    @extend .d-flex;
    @extend .align-items-center;
    @extend .justify-content-between;
    @extend .flex-wrap;
    @extend .mt-5;
    &>div {
        width:50%;
    }
    .icon-wrapper {
        width: 40px;
        height:40px;
        margin-bottom:20px;
        background:$primary;
        border-radius:50%;
        @include transition(all 0.5s ease);
        @include breakpoint(lg) {
            width: 60px;
            height:60px;
            margin-bottom:30px;
        }
        &:hover {
            background:darken($primary, 8);
        }
        svg {
            width: 24px;
            height:24px;
            margin-right:-7px;
            @include breakpoint(lg) {
                width: 34px;
                height:34px;
                margin-right:-10px;
            }
        }
    }
    a {
        @extend .text-dark;
        span {
            border:none;
        }
        &:hover {
            text-decoration:none;
        }
    }
    .nav-next {
        @extend .text-right;
        .icon-wrapper {
            svg {
                @include transform(rotate(180deg));
                margin-right:0px;
                margin-left:-7px;
                @include breakpoint(lg) {
                    margin-left:-10px;
                }
            }
        }
    }
}