.sidebar{
    &.widget-area {
        .widget {
            margin-bottom:50px;
            .widget-title {
                margin-bottom:30px;
            }
            a {
                color: $black;
                &:hover {
                    @extend .text-primary;
                }
            }
            ul {
                li {
                    font-size:20px;
                    @include breakpoint(lg) {
                        font-size:24px;
                    }
                }
            }
            &:last-of-type {
                margin-bottom:0;
            }
        }
    }
}