.site-footer {
    &-bottom {
        &-item {
            width: 100%;
            @include breakpoint(lg) {
                width: auto;
            }
        }
        .menu {
            display:flex;
            flex-wrap:wrap;
            list-style:none;
            align-items:center;
            justify-content:center;
            margin:0;
            &-item {
                width:100%;
                margin-top:8px;
                
                margin-bottom:0;
                @include breakpoint(md) {
                    width:auto;
                    margin-left:24px;
                    margin-top:0;
                }
                &:first-child {
                    @extend .ml-0;
                     margin-top:0;
                }
            }
            a {
                @extend .text-muted;
                &:hover {
                    @extend .text-primary;
                }
            }
        }
    }
    &-with-accordion {
        .widget {
            .widget-title{
                position: relative;
                padding-right:30px;
                
                
                &:after {
                    content: "";
                    position: absolute;
                    right: 0;
                    top: 0;
                    width: 24px;
                    height:24px;
                    background-image: url("data:image/svg+xml,%3Csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath fill-rule='evenodd' clip-rule='evenodd' d='M21.7828 15.4986L20.348 16.9334L12 8.58543L3.65199 16.9334L2.21716 15.4986L11.9986 5.71716L21.7828 15.4986Z' fill='black'/%3E%3C/svg%3E%0A");
                    background-repeat:no-repeat;
                    background-position:center;
                    background-size:16px 16px;
                    @include transform(rotate(180deg));
                    @include transition;
                    @include breakpoint(md) {
                        display:none;
                    }
                }
                &.accordion-open {
                    &:after {
                        @include transform(rotate(0deg));
                    }
                }
                &+div {
                    display:none;
                    @include breakpoint(md) {
                        display:block;
                    }
                }
            }
        }
    }
}