.section-testimonial {
    &-item {
        border:1px solid $primary;
        padding: 30px;
        @include breakpoint(md) {
            padding: 50px;
        }
    }
    .testimonial-single, .testimonial-mutiple {
        max-width:100%;
       visibility:hidden;
        &.slick-initialized {
            visibility:visible;
        }
    }
    .testimonial-mutiple {
        .section-testimonial-item {
            margin:0 15px;
        }

    }
}

