// Blog Listing
.section-blog-listing {
    .blog-posts {
        margin-top:-40px;
        @include breakpoint(md) {
                margin-top:-90px;
            }
        &-item {
            margin-top:40px;
            @include breakpoint(md) {
                margin-top:90px;
            }
        }
    }
}

// Blog dettails
.section-blog-details {
    .entry-title {
        margin-bottom:40px;
    }
    .post-thumbnail {
        margin-bottom:40px;
        img {
            width: 100%;
            height: auto;
        }
    }
    .entry-meta {
        .profile-pic {
            width:46px;
            height: 46px;
            @include breakpoint(lg) {
                width:86px;
                height: 86px;  
            }
        }
    }
    .entry-content {
        .container {
            max-width:100%;
            padding:0;
        }
    }
}